.footer
{
    width: 100%;
    height: 200px;
    background: linear-gradient(90deg, rgb(40, 24, 113),rgb(159, 96, 150));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    margin-top: 200px;
   
}


.socialMedia svg
{
    color: white;
    margin: 20px;
    font-size: 70px;
    cursor: pointer;
}

.footer p
{
    color: white;
}



@media only screen and (max-width:600px) {
    
    .footer svg
    {
        font-size: 50px;
    }
}